.blog .pagination .prev::before, .blog .pagination .prev::after, .blog .pagination .next::before, .blog .pagination .next::after, .archive .pagination .prev::before, .archive .pagination .prev::after, .archive .pagination .next::before, .archive .pagination .next::after {
