  vertical-align: middle;
}
.blog .pagination .prev::before, .archive .pagination .prev::before {
  content: "¬´";
  margin-right: 0.5rem;
}
.blog .pagination .next::after, .archive .pagination .next::after {
  content: "¬ª";
  margin-left: 0.5rem;
}

/* Estilos espec√≠ficos para a p√°gina de Arquivo */
.archive .page-title {
